<template>
  <div component="Save" class="scrollWrap">
    <div class="disTable">
      <div class="disTableCell">
        <div class="disInblock files">
          <!--  S -->
          <van-button icon="plus" :block="true" type="primary" @click="showForm" style="height: 60px;"></van-button>
        
          <van-panel v-for="item in saveFiles" :key="item.id" :title="item.id+':'+item.name" :desc="':'+item.saveTime"
            @click="update(item)">
            <van-button type="default" @click.stop="del(item)"></van-button>
          </van-panel>
          <!--  E -->
        </div>
      </div>
    </div>


    <van-dialog v-model="isShowForm" title="" show-cancel-button @confirm="add">
      <van-field v-model="newName" label="" />
    </van-dialog>

    <FooterNav></FooterNav>
  </div>
</template>

<script>
  import FooterNav from '../../components/FooterNav.vue'
  export default {
    data() {
      return {
        isShowForm: false,
        saveFiles: game.saveFiles,
        newName: "0"
      }
    },
    mounted() {},
    methods: {
      //-
      showForm() {
        this.newName = "" + (game.getMaxSaveId()+1);
        this.isShowForm = true;
      },
      //
      add() {
        let item = {
          id: game.getMaxSaveId() + 1,
          name: this.newName,
          saveTime: (new Date()).toLocaleString()
        };
        game.saveFiles.push(item);
        game.curSave.id = item.id;
        game.curSave.name = item.name;
        game.curSave.saveTime = item.saveTime;
        game.save( item.id, game.curSave );

      },
      //
      update(item) {
        this.$dialog.alert({
          message: '' + item.name + '?',
          showCancelButton: true,
        }).then(() => {
          item.saveTime=(new Date()).toLocaleString();
          game.save(item.id, game.curSave)
        }).catch(() => {

        });
      },
      //
      del(item) {
        this.$dialog.alert({
          message: '' + item.name + '?',
          showCancelButton: true,
        }).then(() => {
          let index = game.saveFiles.findIndex(f => f.id == item.id);
          game.saveFiles.splice(index, 1);
          game.removeSave(item.id);
        }).catch(() => {

        });

      }
    },
    components: {
      FooterNav
    }
  }
</script>
<style>

</style>
