<template>
  <div class="btns">
    <van-button v-show="curPeo" @click="click_cancle" type="default" size="small"></van-button>
    <van-button v-show="round%2!=0" @click="click_end" type="default" size="small"></van-button>
    <van-button @click="click_menu" type="default" size="small"></van-button>

    <van-action-sheet v-model="showOption" :actions="actions" cancel-text="" @select="onSelect" />
  </div>
</template>

<script>
  export default {
    props: ["curPeo", "round"],
    data() {
      return {
        active: 0,
        showOption: false,
        actions: [
          { name: '' },
          { name: '' }
        ]
      }
    },
    methods: {
      click_cancle() {
        this.$emit("click_cancle")
      },

      click_end() {
        this.$dialog.confirm({
            message: '',
          })
          .then(() => {
            this.$emit("click_end")
          })
          .catch(() => {
            // on cancel
          });
      },

      click_menu() {
        this.showOption = true;
      },

      onSelect(item) {

        switch (item.name) {
          case "":
            this.$dialog.confirm({
                message: '',
              })
              .then(() => {
                this.$router.push('/home');
              })
              .catch(() => {
                // on cancel
              });

            break;
          case "":
            this.$dialog.confirm({
                message: '',
              })
              .then(() => {
                this.$router.push('/');
              })
              .catch(() => {
                // on cancel
              });
            break;
        }

      }
    }
  }
</script>

<style lang="scss" scoped>
  .btns {
    position: absolute;
    bottom: 57px;
    left: 0;
    right: 0;

    .van-button {
      margin: 0 5px;
    }
  }
</style>
